<template>
    <div class="fileDownload">
        <h2 class="courseName">{{courseInfo.name}}</h2>
        <span class="source">{{type | parentName}}>{{type | childName}}</span>
        <div class="handleBtn">
            <el-button type="primary" round v-if="handleType!=2" @click="servalHandle">{{handleType | setHandle}}</el-button>
            <el-button type="info" round @click="$emit('back')">返回</el-button>
        </div>
        <div class="downloadDetail">
            <el-table
                ref="multipleTable"
                :data="detailFile"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="getSelect">
                <el-table-column
                    v-if="handleType!=2"
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column
                    prop="courseFileName"
                    label="教学文档"
                    width="600"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">{{handleType | setHandle}}</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import axios from 'axios'
export default {
    data(){
        return{
            selectContent:[]
        }
    },
    computed:{
        ...mapGetters("teach",{
            courseInfo:"getCourseInfo",
            detailFile:"getDetailFile"
        })
    },
    props:["handleType","type","chooseContent"],
    filters:{
        parentName:function(val){
            if(val<=4){
                return '学期授课资源'
            }else{
                return '学期作业文档'
            }
        },
        childName:function(val){
            let arr=['教学文档','教学案例','教学资源','其他','实验报告模板','平时作业模板','其他'];
            return arr[val-1];
        },
        setHandle:function(val){
            if(val===0){
                return '上传'
            }else if(val===1){
                return '下载'
            }else if(val===2){
                return '修改'
            }else if(val===3){
                return '删除'
            }
        },
    },
    methods:{
        ...mapActions('teach',['changeFolderData','deleteFolderData','deleteServalFolderData']),
        isIncludes(attr,val){
            for(let i=0;i<this.detailFile.length;i++){
                if(this.detailFile[i][attr]===val)
                    return false;
            }
            return true;
        },
        // 点击时进行的操作
        handleClick(content){
            let that=this;
            if(this.handleType===1){
                axios.get(`/student/courseFile/download?idList=${[content.courseFileId]}`,{
                responseType: 'blob'
                }).then(res=>{
                    const a = document.createElement('a');
                    a.href = `/student/courseFile/download?idList=${[content.courseFileId]}`;
                    document.body.appendChild(a);
                    a.click();
                    document.body.removeChild(a);
                    this.$message({
                        type: 'success',
                        message:res.data.msg
                    });
                }).catch(err=>{
                    
                })
            }else if(this.handleType===2){
                console.log('content',content)
                 this.$prompt(`修改${content.courseFileName}的名字为`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(({ value }) => {
                    // if(that.isIncludes('courseFileName',value)){
                    //     this.$message({
                    //         type:'info',
                    //         message:'文件夹中已有当前名字的文件夹，请重新命名'
                    //     })
                    // }else{
                    this.changeFolderData({content,courseFileName:value,that:this});
                    // }
                }).catch((err) => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'+err
                    });       
                });
            }else if(this.handleType===3){
                this.deleteFolderData({content,that:this});
            }
        },
        servalHandle(){
            let that=this;
            if(this.handleType==1){
                let idList=[];
                for(let i=0;i<this.selectContent.length;i++){
                    idList.push(this.selectContent[i].courseFileId)
                }
                axios.get(`/student/courseFile/download?idList=${idList}`,{
                    responseType: 'blob'
                }).then(res=>{
                    const a = document.createElement('a');
                    a.href = `/student/courseFile/download?idList=${idList}`;
                    document.body.appendChild(a);
                    a.click();
                    document.body.removeChild(a);
                    this.$message({
                        type: 'success',
                        message:res.data.msg
                    });
                }).catch(err=>{
                    
                })
            }else if(this.handleType==3){
                this.deleteServalFolderData({content,that:this});
            }
        },
        getSelect(val){
            this.selectContent=val
        }
    }
}
</script>

<style lang="less" scoped>
.fileDownload{
    .courseName{
        font-size: 18px;
    }
    .handleBtn{
        float: right;
    }
}
</style>
