<template>
    <div class="directory">
        <button class="directoryBtn el-icon-caret-right" @click="showDrawer"></button>
        <el-drawer
            title="可选操作"
            :visible.sync="drawer"
            :direction="direction"
            :before-close="close"
            :size="'20%'">
            <el-menu
                default-active="1"
                class="el-menu-vertical-demo">
                <el-menu-item index="1" @click="turn(0)">
                    <span slot="title">课程建立</span>
                </el-menu-item>
                <el-menu-item index="2" @click="turn(1)">
                    <span slot="title">我的课程</span>
                </el-menu-item>
                <el-menu-item index="3" @click="turn(2)">
                    <span slot="title">课程作业</span>
                </el-menu-item>
            </el-menu>
        </el-drawer>
    </div>
</template>

<script>
export default {
    data(){
        return{
            drawer: false,
            direction: 'ltr',
        }
    },
    methods:{
        close(done){
            done();
        },
        showDrawer(){
            this.drawer=true;
        },
        turn(type){
            this.drawer=false;
            this.$emit('changeContent',type)
        }
    }
}
</script>

<style lang="less">
.directoryBtn{
    position: fixed;
    left: 0px;
    top: 50%;
    width: 30px;
    height: 80px;
    font-size: 28px;
    color: #fff;
    background-color: #409EFF;
    border: none;
    border-radius: 4px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    outline: none;
    padding: 0px;
    &:hover{
        background: #66b1ff;
        border-color: #66b1ff;
        color: #FFF;
        cursor:pointer;
    }
}
</style>
