<template>
    <div class="courseDetail">
        <div class="searchList" v-if="!searchDetail">
            <h2>{{chooseContent.courseName}}</h2>
            <el-button class="back" type="info" round @click="$emit('back')">返回</el-button>
            <FileUpload v-if="handleType===0" :drawer="drawer" :type="type" :chooseContent="chooseContent"></FileUpload>
            <el-table
                :data="[stateObj]"
                style="width: 100%"
                border>
                <el-table-column 
                    label="学期授课资源"
                    align="center">
                    <el-table-column 
                        label="教学文档"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="handle(scope.row,1)" 
                            type="text" 
                            :disabled="(stateObj.teachingDocument!='无'||handleType==0)?false:true" 
                            size="small">{{stateObj.teachingDocument }}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="教学案例"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="handle(scope.row,2)" 
                            type="text" 
                            :disabled="(stateObj.teachingCase!='无'||handleType==0)?false:true" 
                            size="small">{{stateObj.teachingCase }}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="教学资源"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                            @click="handle(scope.row,3)" 
                            :disabled="(stateObj.teachingResources!='无'||handleType==0)?false:true" 
                            type="text" 
                            size="small">{{stateObj.teachingResources }}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="其他"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="handle(scope.row,4)" 
                                :disabled="(stateObj.otherTeachingResources!='无'||handleType==0)?false:true" 
                                type="text" 
                                size="small">{{stateObj.otherTeachingResources }}</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
                <el-table-column
                    label=""
                    width="2px"
                ></el-table-column>
                <el-table-column 
                    label="学期作业文档"
                    align="center">
                    <el-table-column 
                        label="实验报告模板"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="handle(scope.row,5)" 
                                :disabled="(stateObj.experimentalReportTemplate!='无'||handleType==0)?false:true" 
                                type="text" 
                                size="small">{{stateObj.experimentalReportTemplate }}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="平时作业模板"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="handle(scope.row,6)" 
                                :disabled="(stateObj.dailyWorkTemplate!='无'||handleType==0)?false:true" 
                                type="text" 
                                size="small">{{stateObj.dailyWorkTemplate}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column 
                        label="其他"
                        align="center">
                        <template slot-scope="scope">
                            <el-button 
                                @click="handle(scope.row,7)" 
                                :disabled="(stateObj.otherTermAssignments!='无'||handleType==0)?false:true" 
                                type="text" 
                                size="small">{{stateObj.otherTermAssignments}}</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
                </el-table-column>
            </el-table>
        </div>
        <FileHandle v-if="searchDetail" @back="searchDetail=false" :chooseContent="chooseContent" :handleType="handleType" :type="type"></FileHandle>
    </div>
</template>

<script>
import FileHandle from './fileHandle.vue'
import {mapGetters,mapActions} from 'vuex'
import FileUpload from './fileUpload.vue'
export default {
    data(){
        return{
            drawer:false, // 控制文件上传的模块
            searchDetail:false,
            stateObj:{},
            type:0
        }
    },
    props:["chooseContent","handleType"],
    components:{
        FileHandle,
        FileUpload
    },
    computed:{
        ...mapGetters("teach",{
            courseInfo:"getCourseInfo"
        })
    },
    methods:{
        ...mapActions('teach',['requestDetail']),
        handle(content,type){
            this.type=type;
            // 上传时不进入新的界面
            if(this.handleType===0){
                this.setDrawer()
            }else{
                this.requestDetail({id:this.chooseContent.courseInfoId,type,that:this});
                this.searchDetail=true;
            }
        },
        setDrawer(){
            this.drawer=!this.drawer
        }
    },
    watch:{
        courseInfo(val){
            this.stateObj=JSON.parse(JSON.stringify(val));
            let arr=Object.keys(this.stateObj)
            console.log('arr: ', arr);
            for(let i=0;i<arr.length;i++){
                if(this.stateObj[arr[i]]==0&&this.handleType!=0){
                    this.stateObj[arr[i]]= '无'
                    continue;
                }
                if(this.handleType===0){
                    this.stateObj[arr[i]]='上传'
                }else if(this.handleType===1){
                    this.stateObj[arr[i]]='下载'
                }else if(this.handleType===2){
                    this.stateObj[arr[i]]='修改'
                }else if(this.handleType===3){
                    this.stateObj[arr[i]]='删除'
                }
            }
            console.log('this.stateObj: ', this.stateObj);
        }
    }
}
</script>

<style lang="less" scoped>
.searchList{
    h2{
        display: inline-block;
        font-size: 18px;
    }
    .back{
        float: right;
    }
}
</style>
